<template>
  <div>
    <ul class="tag" v-if="showTag">
      <router-link to="/views/notice/school">
        <li :class="{ active: state == 1 }" @click="selectTag(1)">学校通知</li>
      </router-link>

      <router-link to="/views/notice/department">
        <li :class="{ active: state == 2 }" @click="selectTag(2)">部处通知</li>
      </router-link>

      <router-link to="/views/notice/writing">
        <li :class="{ active: state == 3 }" @click="selectTag(3)">校内发文</li>
      </router-link>
    </ul>
    <div class="tagNull" v-if="showTag"></div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'notice',
  data () {
    return {
      state: 1,
      tagsUrl: [
        '/views/notice/school',
        '/views/notice/department',
        '/views/notice/writing'
      ],
      showTag: false
    }
  },
  methods: {
    selectTag (state) {
      if (state == this.state) {
        return
      }
      this.state = state
    }
  },
  created () {
    const arrIndex = $.inArray(this.$route.path, this.tagsUrl)
    if (arrIndex > -1) {
      this.showTag = true
      this.state = arrIndex + 1
    }
  }

}
</script>



